/*===============================
  Layout
===============================*/
.skills-container {
  padding: 5% 10%;
  display: flex;
  flex-direction: column;
  text-align: left;

  svg { color: white; }
}

.skills-container .skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 50px;
}

.skill {
  align-items: center;
  text-align: left;

  &.current {
    grid-column: 2 / 4;
  }

  h3 {
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .term-text {
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 12px;
    line-height: 1.4;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    .term-gradient {
      font-weight: 700;
      color: #f5b500;
    }
  }

  .chip-title {
    font-size: 1.4rem;
    font-weight: 400;
    color: white;
    padding-right: 10px;
  }
}

/*===============================
  Chip Styles
===============================*/
.chip {
  color: rgb(39, 40, 34) !important;
  background: linear-gradient(90deg, #fddb4d, #f3ae2c) !important;
  font-size: 0.8rem !important;
  height: 25px !important;

  span {
    font-family: 'Courier Prime', monospace;
    color: rgb(39, 40, 34);
  }

  svg { height: 15px !important; }

  &:hover {
    outline: 2px solid currentColor !important;
  }
}

.flex-chips {
  line-height: 2;
  margin: 0.5em 0;

  .MuiChip-root { margin-right: 0.5em; }
  .MuiChip-outlined { background-color: #e0e0e0; }
  .MuiChip-outlined span { color: rgb(39, 40, 34); }
}

/*===============================
  Text Modes
===============================*/
.light-mode { color: #000; }
.dark-mode  { color: #fff; }

/*===============================
  Cyber Text
===============================*/
.cyber-text {
  font-size: 2.0rem;
  color: #fff;
  margin-top: -10px;

  .cyber-gradient {
    font-weight: 700;
    background: linear-gradient(90deg, #50caff, #0070f3);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

/*===============================
  Logo Slider
===============================*/
.logo-slider {
  overflow: hidden;
  width: 100%;
  max-width: 700px;
  margin: 0 auto 1rem;
  position: relative;
  mask-image: linear-gradient(
    to right,
    transparent,
    black 20%,
    black 80%,
    transparent
  );
  -webkit-mask-image: linear-gradient(
    to right,
    transparent,
    black 20%,
    black 80%,
    transparent
  );
}

.logo-track {
  display: flex;
  width: fit-content;
  animation: scroll 20s linear infinite;
}

.logo-track img {
  width: 75px;
  height: 75px;
  margin-right: 20px;
  border-radius: 2px;
  transition: transform 0.3s ease;

  &.logo-ansys {
    width: 280px !important;
    height: auto   !important;
    margin-right: 25px !important;
  }

  &:hover { transform: scale(1.1); }
}

@keyframes scroll {
  0%   { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

/*===============================
  Logos
===============================*/
.waterloo-logo,
.cybertruck-logo {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 2px;
  object-fit: contain;
}

.waterloo-logo {
  max-height: 110px;
}

.cybertruck-logo {
  max-height: 130px;
  margin: 0 auto 1rem;
}

/*===============================
  Responsive
===============================*/
/* Tablet down */
@media (max-width: 768px) {
  .skills-container {
    display: block;
    padding: 0 5%;
  }

  .skills-container .skills-grid {
    display: block;
  }

  .skills-container .skill {
    padding-bottom: 5%;
  }

  .skills-container .skill h3         { font-size: 1.4rem !important; }
  .skills-container .skill .term-text { font-size: 1.6rem !important; }
  .chip-title                          { font-size: 1rem !important; }
  .chip                                { font-size: 0.7rem !important; height: 22px !important; padding: 0 10px !important; }
}

/* iPhone / small mobile only */
@media only screen and (max-width: 414px) {
  .cyber-text { font-size: 1.4rem !important; }
  .waterloo-logo { max-width: 250px !important; }
  .cybertruck-logo { max-height: 100px  !important; }
}

.skills-container .skill h3 {
  white-space: nowrap;
}

.logo-slider {
  margin: 1rem 0 0 0;   /* instead of “0 auto 1rem”, this pins it left */
}

.blue-term {
  display: inline-block;          /* shrink to fit */
  font-weight: bold;
  line-height: 1;

  /* gradient text */
  background: linear-gradient(270deg, #00d2ff 0%, #3a47d5 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  /* default desktop size: between 4rem and 8rem */
  font-size: clamp(4rem, 10vw, 6rem);
  margin: 1rem 0;                 /* vertical breathing room */
}

/* tablet adjustment */
@media (max-width: 768px) {
  .blue-term {
    font-size: clamp(4rem, 15vw, 6rem);
  }
}

/* phone adjustment */
@media (max-width: 414px) {
  .blue-term {
    font-size: clamp(2rem, 25vw, 4rem);
  }
}

/* Mobile font fix for Option and Honors */
@media only screen and (max-width: 480px) {
  .skills-container .skills-grid .skill .term-text {
    font-size: 1.3rem !important;
    line-height: 1.3 !important;
  }

  .skills-container .skills-grid .skill .term-text .term-gradient {
    font-size: 1.3rem !important;
    line-height: 1.3 !important;
  }
}

.italic-text {
  font-style: italic; // only if italic is loaded
}